'use client';
import { costemStorage } from "@/utils";
import { useEffect, useState } from "react"

export const useAuth = () => {
    const [ auth, setAuth ] = useState(costemStorage.local.get('authPw',false));
    const [ loding, setLoding ] = useState(false);

    const authPath = (pw:string) => {
        setLoding(true);
    }

    return {
        isAuth: auth === 'true',
        login: authPath,
        loding: loding,
    };
}